import {Map} from 'immutable'
import * as O from 'fp-ts/lib/Option'
import * as A from 'fp-ts/Array'
import {
    cc,
    CubeCoords,
    CubeCoordsAsString,
    cubeCoordsToString,
    distance,
    ZeroInCube
} from '../hex-grids/cube-coords'
import {evenQOffsetToCubeCoords, oddQOffsetToCubeCoords} from '../hex-grids/conversions'
import {Brand, minusToPlusN} from '../hex-grids/utils'
import {CellValue} from './game-logic'

export type Size = Brand<number, 'board-size'>
export type Board<T> = {
    readonly size: Size
    readonly data: Map<CubeCoordsAsString, T>
}

export function createBoard<T>(size: Size, valueGenerator: (c: CubeCoords) => T): Board<T> {
    let data: Map<CubeCoordsAsString, T> = Map()

    minusToPlusN(size, (x) => {
        minusToPlusN(size, (y) => {
            minusToPlusN(size, (z) => {
                if (x + y + z === 0) {
                    const coords = cc(x, y, z)
                    const cubeAsString = cubeCoordsToString(coords)
                    const value = valueGenerator(coords)
                    data = data.set(cubeAsString, value)
                }
            })
        })
    })

    return { size, data }
}

export function inBounds<T>(board: Board<T>, c: CubeCoords): boolean {
    return distance(ZeroInCube, c) <= board.size
}

export function cellAt<T>(board: Board<T>, c: CubeCoords): O.Option<T> {
    const result = board.data.get(cubeCoordsToString(c))
    return result === undefined ? O.none : O.some(result)
}

export function mapBoardInOffsetQ<T, R>(board: Board<T>, f: (c: CubeCoords, value: O.Option<T>) => R): readonly R[] {
    const convertF = board.size % 2 === 0 ? oddQOffsetToCubeCoords : evenQOffsetToCubeCoords

    return A.range(-board.size, board.size).flatMap((r) =>
        A.range(-board.size, board.size).map((c) => {
            const coords: CubeCoords = convertF(r, c)
            const result = cellAt(board, coords)
            return f(coords, result)
        })
    )
}

function mapSlicesBySomething<T>(
    board: Board<T>,
    f: (data: readonly T[]) => readonly T[],
    create: (first: number, second: number, third: number) => CubeCoords,
): Board<T> {
    let result = board

    A.range(-board.size, board.size).forEach((first) => {
        const values: T[] = []
        const indices: number[] = []

        A.range(-board.size, board.size).forEach((second) => {
            const third = -first-second
            const coords = create(first, second, third)
            const cell = cellAt(board, coords)

            // eslint-disable-next-line array-callback-return
            O.map<T, void>((v) => {
                values.push(v)
                indices.push(second)
            })(cell)
        })

        const converted = f(values)

        indices.forEach((second, idx) => {
            const third = -first-second
            const coords = create(first, second, third)
            result = updated(result, coords, converted[idx])
        })
    })

    return result
}

export function mapSlicesByX<T>(board: Board<T>, f: (data: readonly T[]) => readonly T[]): Board<T> {
    return mapSlicesBySomething(board, f, (first: number, second: number, third: number) => cc(first, third, second))
}

export function mapSlicesByY<T>(board: Board<T>, f: (data: readonly T[]) => readonly T[]): Board<T> {
    return mapSlicesBySomething(board, f, (first: number, second: number, third: number) => cc(third, first, second))
}

export function mapSlicesByZ<T>(board: Board<T>, f: (data: readonly T[]) => readonly T[]): Board<T> {
    return mapSlicesBySomething(board, f, (first: number, second: number, third: number) => cc(second, third, first))
}

export function updated<T>(board: Board<T>, c: CubeCoords, newValue: T): Board<T> {
    if (inBounds(board, c)) {
        return {
            size: board.size,
            data: board.data.update(cubeCoordsToString(c), () => newValue),
        }
    } else {
        throw new Error(`Cannot update ${c} as it is out of bounds for ${board}`)
    }
}

export function map<T, R>(board: Board<T>, f: (c: CubeCoords, v: T) => R): R[] {
    return flatMap(board, (c, v) => [f(c, v)])
}

export function flatMap<T, R>(board: Board<T>, f: (c: CubeCoords, v: T) => R[]): R[] {
    return A.range(-board.size, board.size).flatMap(x =>
        A.range(-board.size, board.size).flatMap(y =>
            A.range(-board.size, board.size).flatMap(z => {
                if (x + y + z === 0) {
                    const coords = cc(x, y, z)
                    const value = board.data.get(cubeCoordsToString(coords))
                    if (value === undefined) {
                        throw new Error(`Expected to find ${coords}`)
                    } else {
                        return f(coords, value)
                    }
                } else {
                    return []
                }
            })
        )
    )
}

export function maxNumber(board: Board<CellValue>): CellValue {
    const numbers = map(board, (c, v) => v)
    return Math.max(...numbers) as CellValue
}

export function filterCoordinates<T>(board: Board<T>, p: (v: T) => boolean): CubeCoords[] {
    return flatMap(board, (c, v) => p(v) ? [c] : [])
}
