import React, {KeyboardEvent, useEffect, useRef} from 'react'
import {newMoveAction} from './actions'
import {hex2048Selector, useAppDispatch} from '../../app/store'
import {LevelSelectionButtons} from './LevelSelectionButtons'
import {ShowBoard} from './ShowBoard'
import './Game.scss'
import {Direction} from '../hex-grids/direction'
import {useSelector} from 'react-redux'
import {toDisplay} from './game-logic'
import {backgroundOf} from './colors'
import {Game2048State} from './state'

export function Game() {
    const dispatch = useAppDispatch()
    const hex2048: Game2048State = useSelector(hex2048Selector)
    const maxNumberSeen = hex2048.maxNumberSeen

    function handleKeyDown<T>(e: KeyboardEvent<T>) {
        if (e.code === 'KeyC') {
            dispatch(newMoveAction(Direction.S))
        } else if (e.code === 'KeyV') {
            dispatch(newMoveAction(Direction.SE))
        } else if (e.code === 'KeyX') {
            dispatch(newMoveAction(Direction.SW))
        } else if (e.code === 'KeyE') {
            dispatch(newMoveAction(Direction.N))
        } else if (e.code === 'KeyW') {
            dispatch(newMoveAction(Direction.NW))
        } else if (e.code === 'KeyR') {
            dispatch(newMoveAction(Direction.NE))
        }
    }

    const container = useRef<HTMLDivElement>(null)

    useEffect(() => container.current?.focus())

    return (
        <div ref={container} className='container game-board' tabIndex={-1} onKeyDown={handleKeyDown}>
            <LevelSelectionButtons/>
            <ShowBoard/>
            <div>
                Max number seen:
                &nbsp;
                <span
                    className='has-text-weight-bold'
                    style={{backgroundColor: backgroundOf(maxNumberSeen), padding: '4px'}}
                >
                    {maxNumberSeen > 0 ? toDisplay(maxNumberSeen) : 'none'}
                </span>
            </div>
        </div>
    )
}
