import {useSelector} from 'react-redux'
import React, {CSSProperties} from 'react'
import {newMoveAction} from './actions'
import {hex2048Selector, useAppDispatch} from '../../app/store'
import './ShowBoard.scss'
import * as O from 'fp-ts/Option'
import {CubeCoords, cubeCoordsToString, dominantDirection} from '../hex-grids/cube-coords'
import {Direction} from '../hex-grids/direction'
import {mapBoardInOffsetQ} from './board'
import {CellValue, toDisplay} from './game-logic'
import {backgroundOf} from './colors'
import {Game2048State} from './state'

export function ShowBoard() {
    const hex2048: Game2048State = useSelector(hex2048Selector)
    const board = hex2048.board
    const dispatch = useAppDispatch()

    function hexClicked(direction: O.Option<Direction>) {
        O.map<Direction, void>((v) => dispatch(newMoveAction(v)))(direction)
    }

    function createHex(c: CubeCoords, v: O.Option<CellValue>) {
        const coordsAsString = cubeCoordsToString(c)
        const direction = dominantDirection(c)
        const cursor = O.fold<Direction, string>(() => 'not-allowed', (d) => `${d.toLowerCase()}-resize`)(
            direction)

        const style: CSSProperties = O.fold<CellValue, CSSProperties>(() =>
            ({
                display: 'none',
            }),
        (n: CellValue) =>
            ({
                backgroundColor: backgroundOf(n),
                cursor: cursor,
            })
        )(v)

        const rendered = O.fold(() => '', toDisplay)(v)

        return (<div
            key={coordsAsString}
            data-coords={coordsAsString}
            className="hex"
            style={style}
            onClick={() => hexClicked(direction)}
        >
            {rendered}
        </div>)
    }

    return (
        <div id={'boardHexGrid-' + board.size}>
            <div className="hexCrop">
                <div className="hexGrid">
                    {mapBoardInOffsetQ(board, createHex)}
                </div>
            </div>
        </div>
    )
}
