import * as O from 'fp-ts/Option'
import {Direction} from './direction'
import {Brand} from './utils'

export type X = Brand<number, 'cc-x'>
export type Y = Brand<number, 'cc-y'>
export type Z = Brand<number, 'cc-z'>

export interface CubeCoords {
    readonly x: X
    readonly y: Y
    readonly z: Z
}

export function cc(x: number, y: number, z: number): CubeCoords {
    if (x + y + z === 0) {
        return {x: x as X, y: y as Y, z: z as Z}
    } else {
        throw new Error(`Inconsistent cube coordinates - ${x} ${y} ${z}`)
    }
}

export const ZeroInCube = cc(0, 0, 0)

export type CubeCoordsAsString = Brand<string, 'cube-coords-as-string'>

const Separator = '_'
export function cubeCoordsToString(c: CubeCoords): CubeCoordsAsString {
    return c.x + Separator + c.y + Separator + c.z as CubeCoordsAsString
}

export function dominantDirection(c: CubeCoords): O.Option<Direction> {
    if ((c.x === 0) && (c.y === 0) && (c.z === 0)) {
        return O.none
    } else if (c.x === 0) {
        return O.some(c.y > 0 ? Direction.N : Direction.S)
    } else if (c.y === 0) {
        return O.some(c.x > 0 ? Direction.NE : Direction.SW)
    } else if (c.z === 0) {
        return O.some(c.y > 0 ? Direction.NW : Direction.SE)
    } else {
        return O.none
    }
}

const abs = Math.abs
export function distance(a: CubeCoords, b: CubeCoords): number {
    return (abs(a.x - b.x) + abs(a.y - b.y) + abs(a.z - b.z)) / 2
}

