import {newLevelAction} from './actions'
import React from 'react'
import {useAppDispatch} from '../../app/store'
import {Size} from './board'

export function LevelSelectionButtons() {
    const dispatch = useAppDispatch()

    return (
        <div className='buttons has-addons'>
            <button className='button' disabled={true}>
                New Level
            </button>
            {[1, 2, 3, 4].map((level) =>
                (<button
                    className='button'
                    key={level}
                    onClick={() => dispatch(newLevelAction(level as Size))}
                >
                    {level}
                </button>)
            )}
        </div>
    )
}
