import {Board, createBoard, filterCoordinates, maxNumber, Size, updated} from './board'
import {BoardAction, MOVE_ACTION, NEW_LEVEL_ACTION} from './actions'
import {Game2048State} from './state'
import {CellValue, mergeAndMoveInDirection} from './game-logic'

function pickRandomN<T>(array: readonly T[], n: number): T[] {
    return array
        .map((a) => ({order: Math.random(), value: a}))
        .sort((a, b) => a.order - b.order)
        .map((a) => a.value)
        .slice(0, n)
}

function addRandomValues(board: Board<CellValue>): Board<CellValue> {
    const empty = filterCoordinates(board, (v) => v === 0 as CellValue)
    const selected = pickRandomN(empty, 2)
    const value = (Math.random() > 0.5 ? 1 : 2) as CellValue // either 2^1 == 2 or 2^2 == 4
    let result = board
    selected.forEach((c) => {
        result = updated(result, c, value)
    })
    return result
}

const initialSize = 2 as Size
const initialState: Game2048State = newState(initialSize)

function newState(size: Size): Game2048State {
    const board = addRandomValues(createBoard(size, () => 0 as CellValue))

    return {
        board,
        maxNumberSeen: maxNumber(board)
    }
}

export function hex2048Reducer(state: Game2048State = initialState, action: BoardAction) {
    const board = state.board

    switch (action.type) {
    case MOVE_ACTION: {
        const newBoard = mergeAndMoveInDirection(board, action.direction)
        if (newBoard.data.equals(board.data)) { // nothing changed
            // TODO: play a sad beep
            return state
        } else {
            const maxOnThis = maxNumber(newBoard)
            const maxNumberSeen = Math.max(state.maxNumberSeen, maxOnThis)
            return { ...state, maxNumberSeen, board: addRandomValues(newBoard) }
        }
    }
    case NEW_LEVEL_ACTION:
        return newState(action.size)
    default:
        return state
    }
}
