import {Direction} from '../hex-grids/direction'
import {Brand, reverse} from '../hex-grids/utils'
import {Board, mapSlicesByX, mapSlicesByY, mapSlicesByZ} from './board'

export type CellValue = Brand<number, 'cell-value'>

export function mergeAndMoveInDirection(board: Board<CellValue>, d: Direction): Board<CellValue> {
    console.log(`Move and merge ${d}`)
    const moved = moveInDirection(board, d)
    const merged = mergeInDirection(moved, d)
    return moveInDirection(merged, d)
}

function mergeLeft(array: readonly CellValue[]): readonly CellValue[] {
    if (array.length <= 1) {
        return array
    } else {
        const a = array[0]
        const b = array[1]
        const rest = array.slice(2)
        if (a === b) {
            if (a === 0) { // empty field
                return [a, b, ...mergeLeft(rest)]
            } else { // we do the merge
                return [a + 1 as CellValue, 0 as CellValue, ...mergeLeft(rest)]
            }
        } else {
            return [a, ...mergeLeft([b, ...rest])]
        }
    }
}

function mergeRight(array: readonly CellValue[]): readonly CellValue[] {
    return reverse(mergeLeft(reverse(array)))
}

export function mergeInDirection(board: Board<CellValue>, d: Direction): Board<CellValue> {
    switch (d) {
    case Direction.NW:
        return mapSlicesByZ(board, mergeLeft)
    case Direction.SE:
        return mapSlicesByZ(board, mergeRight)
    case Direction.N:
        return mapSlicesByX(board, mergeLeft)
    case Direction.S:
        return mapSlicesByX(board, mergeRight)
    case Direction.NE:
        return mapSlicesByY(board, mergeLeft)
    case Direction.SW:
        return mapSlicesByY(board, mergeRight)
    }
}

function shiftLeft(array: readonly CellValue[]): readonly CellValue[] {
    const missingZeros = (length: number): readonly CellValue[] => new Array(length).fill(0 as CellValue)
    const filtered = array.filter((x) => x > 0)
    return [...filtered, ...missingZeros(array.length - filtered.length)]
}

function shiftRight(array: readonly CellValue[]): readonly CellValue[] {
    return reverse(shiftLeft(reverse(array)))
}

export function moveInDirection(board: Board<CellValue>, d: Direction): Board<CellValue> {
    switch (d) {
    case Direction.NW:
        return mapSlicesByZ(board, shiftLeft)
    case Direction.SE:
        return mapSlicesByZ(board, shiftRight)
    case Direction.N:
        return mapSlicesByX(board, shiftLeft)
    case Direction.S:
        return mapSlicesByX(board, shiftRight)
    case Direction.NE:
        return mapSlicesByY(board, shiftLeft)
    case Direction.SW:
        return mapSlicesByY(board, shiftRight)
    }
}

export function toDisplay(n: CellValue): string {
    return n ? `${Math.pow(2, n)}` : ''
}
