import {cc, CubeCoords} from './cube-coords'

export function oddQOffsetToCubeCoords(r: number, c: number): CubeCoords {
    const x = c
    const z = r - (c - (c & 1)) / 2
    const y = -x-z
    return cc(x, y, z)
}

export function evenQOffsetToCubeCoords(r: number, c: number): CubeCoords {
    const x = c
    const z = r - (c + (c & 1)) / 2
    const y = -x-z
    return cc(x, y, z)
}
