import {Direction} from '../hex-grids/direction'
import {Size} from './board'

export const MOVE_ACTION = 'hex-2048/move'
export const NEW_LEVEL_ACTION = 'hex-2048/newLevel'

export interface MoveAction {
    type: typeof MOVE_ACTION
    direction: Direction
}

export interface NewLevelAction {
    type: typeof NEW_LEVEL_ACTION
    size: Size
}

export type BoardAction = MoveAction | NewLevelAction

export function newMoveAction(direction: Direction): MoveAction {
    return {
        type: MOVE_ACTION,
        direction,
    }
}

export function newLevelAction(size: Size): NewLevelAction {
    return {
        type: NEW_LEVEL_ACTION,
        size,
    }
}
