import {CellValue} from './game-logic'

const colors = [
    '#D0D0D0', // empty
    '#B0D8A4', // 2
    '#CADB9E', // 4
    '#E4DE97', // 8
    '#FEE191', // 16
    '#FEC181', // 32
    '#FDA070', // 64
    '#FD8060', // 128
    '#F9715E', // 256
    '#F4615C', // 512
    '#F0525A', // 1024
    '#EB4258', // 2048
]

export function backgroundOf(n: CellValue): string {
    return colors[n] || colors[colors.length - 1]
}
